.search-from-navbar-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	position: fixed;
	top: 0;
	left: 0;
	background-color: white;
	width: 100vw;
	height: 55vh;


	.search-from-navbar-row {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
	}

	.x-icon {
		position: absolute;
		left: 1%;
		cursor: pointer;
		padding: 20px;
	}

	.search-from-navbar-title {
		font-size: 18px;
		font-style: normal;
		font-weight: 200;
		line-height: normal;
		letter-spacing: 1.92px;
	}

	.user-search-from-navbar {
		width: 70vw;
		padding: 10px;
		border: 1px solid black;
		border-radius: 10px;
		text-align: center;
		background-color: rgba(255, 255, 255, 0.7);
		text-indent: 25px;
		background: url('../../../shared/assets/icons/search.svg') no-repeat left;
		background-size: 20px;
		background-position: 5px;
	}

	.search-from-navbar-icon {
		position: absolute;
		left: 15%;
		top: 19%;
		transform: translateY(-50%);
		width: 20px;
		height: 20px;
	}

	/* Media Query for Desktop */
	@media (min-width: 768px) {
		height: 40vh;

		.user-search-from-navbar {
			width: 50vw;
		}
	}

	/* Media Query for Mobile */
	@media (max-width: 767px) {
	}
}
